export default {
	data() {
		return {
			activeTab: 0
		};
	},
	props:{
		type:{
			type: String,
			default:'customer'
		},
		index:{
			type:Number,
			default:0
		}
	},
	mounted() {
		this.activeTab = this.index
	},
	methods:{
		switchTab(index){
			const token = uni.getStorageSync('token')
			const isLogin = uni.getStorageSync('isLogin')
			if(index !== 0){
				if(!token || isLogin !== 0 ){
					this.$emit('showLogin')
					return 
				}
			}
			this.activeTab = index
			this.$emit('switchTab', index)
		}
	}
}
